body{
    background-color: blanchedalmond;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    line-height: 1.5;
    border:0;
    margin:0;
    padding:0;
}
article{
    font-size: 24px;
    color:pink;
    &.widget{
        width: 250px;
        height: 200px;
        padding: 10px;
        border-radius: 5px;
        border: 1px solid #c0c0c0;
        &:hover{
            transition: background-color 1s ease-in-out;
            background-color: red !important;
        }
    }
}
